import React from 'react';
import { Link } from "react-router-dom";
import logo from '../images/logo.png';
import '../styles/Header.css';
let origin = window.origin.toString();

class Header extends React.Component {

    state = {

    };

    componentDidMount () {

    }

    toggleClass = (e) => {
        //e.preventDefault();
        let otherElems = document.querySelectorAll('.header__elem1__link');
        otherElems.forEach( elem => elem.classList.remove('--selected'));
        let elem = e.currentTarget.classList;
        elem.add('--selected');
    };

    render () {
        return(

            <div className={"header"}>
                <div className={"header__elem1"}>
                    <div className={"header__elem1__logo"}> <img src={logo} width={40} height={40}/></div>
                    <Link to="/aradschool" className={"header__elem1__link quick"} onClick={this.toggleClass}> ÏôïÎ¶ΩÏïÑÎùºÎìú </Link>
                    <Link to="/chuseok" className={"header__elem1__link quick"} onClick={this.toggleClass}> Ï∂îÏÑùÌå®ÌÇ§ÏßÄ </Link>
                    <div className={"nav-vertical-bar"}>  </div>
                    <Link to="/Wd" className={"header__elem1__link quick"} onClick={this.toggleClass}> Í≤ÄÏÉâ </Link>
                    <Link to="/about" className={"header__elem1__link quick"} onClick={this.toggleClass}> Ï†ïÎ≥¥ </Link>

                </div>

                <div className={"header__elem2"}>
                    <div> { origin.includes('osh') ? 'public' : 'live-dev' } </div>
                </div>
            </div>
            )
    }
}

export default Header;