import React from 'react';
import socketIO from 'socket.io-client'
import '../styles/Quick.css';

let TITLE = 'TYPE IPC NAME';
let AVATAR = {
    head : "",
    body : [""],
    tail : ""
};


function AddComma(str) {
    return Number(str).toLocaleString('en');
}

class RealtimeSearch extends React.Component {

    state = {
        snapshot : {},
        prevSnapshot : {},

        summary : {},
        prevSummary : {},

        cuttline : 7100000,
        timeStamp : 0,
        connect : 0,
        //DISPLAY
        shown : true,
        color : '--white',
        //
        tempAlramVar : 0
    };

    socket = '';

    componentDidMount () {
        TITLE = this.props.TITLE;
        AVATAR = this.props.AVATAR;
        console.group(TITLE);
        console.log(`üîå MOUNT`);

        let temp_url = window.location.origin.split(':');
        let temp_url2 = temp_url[0] + temp_url[1];

        this.socket = socketIO.connect(temp_url[1], {transports: ['websocket']}, {'reconnectionAttempts': 5});
        const socket = this.socket;
        Notification.requestPermission();

        socket.on('connect', function () {
            console.log(`‚úÖ ON CONNECT`);
            socket.emit(`getFirstData-${TITLE}`, '');
        });

        socket.on('connect_failed', function() {
            console.log(`‚úÖ CONNECT_FAILED`);
        });

        socket.on('reconnect_failed', () => console.log(`‚úÖ RECONNECT_FAILED`));

        socket.on(`snapshot-${TITLE}`, (received_snapshot, timeStamp) => {
            console.log(`üîª ON SNAPSHOT`);
            this.setState({color : '--green'}, () => {setTimeout(()=> this.setState({color:'--black'}), 500)});

            let snapshot = received_snapshot;
            let prevSnapshot = this.state.snapshot;
            let hash = this.hashCode(JSON.stringify(snapshot));
            let prevHash = this.hashCode(JSON.stringify(prevSnapshot));
            let isPrevSnapshotExist = Object.keys(this.state.prevSnapshot).length !== 0;

            //
            console.log(snapshot);

            let lowest = Object.keys(snapshot).reduce((acc, cuv) => {
                acc[cuv] = snapshot[cuv][0].unitPrice;
                return acc;
            }, {});
            let prevLowest = Object.keys(prevSnapshot).reduce((acc, cuv) => {
                acc[cuv] = prevSnapshot[cuv][0].unitPrice;
                return acc;
            }, {});
            let quantity = Object.keys(snapshot).reduce((acc, itemName) => {
                acc[itemName] = snapshot[itemName].reduce((quan, cuv) => {
                    quan += cuv.count;
                    return quan;
                },0);
                return acc;
            },{});

            let summary = {
                lowest : lowest,
                prevLowest : prevLowest,
                quantity : quantity,
                hash : hash
            };

            //

            if(!isPrevSnapshotExist) this.setState({
                snapshot : snapshot,
                prevSnapshot : snapshot,

                summary : summary,
                prevSummary : summary,

                timeStamp : timeStamp,
                hash : hash
            },  () => {});

            else {
                let prevSnapshot = this.state.snapshot;
                let prevSummary = this.state.summary;
                this.setState({
                    snapshot : snapshot,
                    prevSnapshot: prevSnapshot,

                    summary : summary,
                    prevSummary : prevSummary,

                    timeStamp : timeStamp,
                    hash : hash
                }, this.notiCallback);
            }
        });

    }

    componentWillUnmount = () => {
        console.log(`‚ùé UNMOUNT AND DISCONNECT`);
        this.socket.disconnect();
        console.groupEnd();
    };

    notiCallback = () => {
        let lowest = this.state.summary.lowest;
        let calcValue = 1*(lowest[AVATAR.head]) - 0.97*(this.sumOfBody(lowest, AVATAR.body));
        let avatarStuffPrice = lowest[AVATAR.tail];

        console.log(calcValue, this.state.cuttline, this.state.hash, this.hashConvert(this.state.hash));

        if(((calcValue <= this.state.cuttline) || (avatarStuffPrice <= this.state.cuttline)) && this.state.tempAlramVar === 0) {
            let callback = () => {
                setTimeout(() => this.setState({tempAlramVar: 0}), 15000);
            };
            this.setState({tempAlramVar: 1},callback);
            new Notification('ÏïåÎ¶º', { body: `${this.hashConvert(this.state.hash)}Ôº†${Math.floor(calcValue)}` });

        }
    };

    hashCode = (s) => {
        return s.split("").reduce(function(a,b){a=((a<<5)-a)+b.charCodeAt(0);return a&a},0);
    };

    hashConvert = (num) => {
        return String(Math.abs(num)).split('').map((elem)=> 1*elem).reduce((acc,cuv) => (acc += String.fromCharCode(97+cuv), acc),'');
    };

    signString = (prev, curr) => {
        let diff = curr - prev;
        let sign = Math.sign(diff) + 0;
        if(sign === 0)  return ['--zero', '', '-'];
        if(sign === 1)  return ['--plus', '‚Üë ', Math.abs(diff).toLocaleString('en')];
        if(sign === -1) return ['--minus', '‚Üì ', Math.abs(diff).toLocaleString('en')];

    };
    toggle = (e) => {
        console.log(e.target.classList);
        let elem = document.querySelector(`#${e.target.getAttribute('id')}`);
        if(elem.style.display === 'none'){
            elem.style.display = 'flex';
        } else {
            elem.style.display = 'none';
        }
    };

    isOverCuttLine = (price) => {
        return !!price === false ? false : Math.sign(price - this.state.cuttline) < 1;
    };

    inputVerify = (e) => {
        let numChuck = Number.isInteger(1*e.target.value);
        if(numChuck) return this.setState({cuttline: Number(e.target.value)});
    };

    sumOfBody = (lowest, body) => {
        if(Object.keys(lowest).length === 0) return 0;
        if(!Array.isArray(body)) return 0;
        let sumOfBody = body.reduce((acc, cuv) => {
            if(lowest[cuv] === undefined) console.warn(`check member varible of AVATAR`);
            acc += lowest[cuv];
            return acc;
        }, 0);
        return sumOfBody;
    };

    render() {
        let snapshot = this.state.snapshot;
        let lowest = this.state.summary.lowest || {};
        let prevLowest = this.state.prevSummary.lowest || {};
        let quantity = this.state.summary.quantity || {};

        let realAvatarPrice = 1*(lowest[AVATAR.head]) - 0.97*(this.sumOfBody(lowest, AVATAR.body));
        let didOverCuttLine = this.isOverCuttLine(realAvatarPrice);

        return (
            <div id={"quick"}>
                <div className={"wrapper"}>
                    <div className={"lowest"}>
                        <div className={"attribute row --shown-"+this.state.shown} onClick={() => this.setState({shown: !this.state.shown})}>
                            <div className={"attribute cell --long"}> Í≤ÄÏÉâÏñ¥ </div>
                            <div className={"attribute cell --short"}> Í∞ÄÍ≤© </div>
                            <div className={"attribute cell --short"}> Î≥ÄÎèôÌè≠ </div>
                            <div className={"attribute cell --extra-short"}> Î¨ºÎüâ </div>
                        </div>
                        {
                            Object.keys(lowest).map((itemName, idx) => {
                                let price_typeNumber = lowest[itemName];
                                let price = AddComma(lowest[itemName]);
                                let sign = this.signString(prevLowest[itemName], lowest[itemName]);
                                let isAvatar = itemName.includes(AVATAR.tail);

                                return(<div className={"data row " +(this.isOverCuttLine(price_typeNumber)&&isAvatar ? '--red' : '--none')} key={idx}>
                                    <div className={"data cell --long --elipsis"}> { Object.keys(snapshot).length >= 1 ? snapshot[itemName][0].itemName : itemName } </div>
                                    <div className={"data cell --short --num"}> { price  } </div>
                                    <div className={"data cell --short --num "+sign[0]}> {sign[1]}{ sign[2] } </div>
                                    <div className={"data cell --extra-short --num "}> { quantity[itemName] } </div>
                                </div>)
                            })
                        }
                        <div className={"data row "+(didOverCuttLine ? '--red' : '--none')}>
                            <div className={"data cell --long"}> { "Ïã§Ï†ú ÏïÑÎ∞îÌÉÄÍ∞í" } </div>
                            <div className={"data cell --short --num "}> { Math.floor(realAvatarPrice || 0).toLocaleString('en') || 0 } </div>
                            <div className={"data cell --short"}> {  } </div>
                            <div className={"data cell --extra-short"}> {  } </div>
                        </div>

                        <div className={"lowest-tail --flex"}>
                            <div className={"--flex --space-between"}>
                                <div className={"--flex"}>

                                    <div><i className={"fas fa-caret-square-down "+this.state.color+" --temp"}></i></div>
                                    <div> { new Date(this.state.timeStamp || 0).toLocaleTimeString() } </div>
                                </div>
                                <div> { this.hashConvert(this.state.hash) } </div>
                            </div>


                            <div className={"--flex --relative"}>
                                <i className="fas fa-eye --inner-eye"></i>
                                <input className={"--simpleInput"} value={this.state.cuttline} onChange={this.inputVerify}/>
                            </div>
                        </div>
                    </div>

                    <div className={"snapshot"}>
                    </div>

                </div>
                <div className={"honey-bee"}></div>
            </div>
        )
    }
}



export default RealtimeSearch;